import React from "react";
import { useMutation } from "@apollo/client";
import { Button, Form, Input, Typography } from "antd";
import { PASSWORD_CHANGE } from "../../api/mutations";
import {
  PasswordChange,
  PasswordChangeVariables,
} from "../../api/types/PasswordChange";
import Errors from "../molecules/Errors";

const { Title, Text } = Typography;

const ChangePassword = () => {
  const [passwordChange, { loading, data }] = useMutation<
    PasswordChange,
    PasswordChangeVariables
  >(PASSWORD_CHANGE);
  return (
    <>
      <Title level={2}>Modificar contrasea</Title>
      <Errors errors={data?.passwordChange?.errors} />
      {data?.passwordChange?.success && (
        <Text type="success">Contrasea cambiada con xito</Text>
      )}
      <Form
        onFinish={(values) => {
          passwordChange({
            variables: {
              ...values,
            },
          });
        }}
      >
        <Form.Item
          label="Contrasea actual"
          name="oldPassword"
          rules={[{ required: true }]}
        >
          <Input.Password />
        </Form.Item>
        <Form.Item
          label="Nueva contrasea"
          name="newPassword1"
          rules={[{ required: true }, { min: 8 }]}
        >
          <Input.Password />
        </Form.Item>
        <Form.Item
          label="Repetir nueva contrasea"
          name="newPassword2"
          rules={[{ required: true }, { min: 8 }]}
        >
          <Input.Password />
        </Form.Item>
        <Form.Item>
          <Button
            htmlType="submit"
            type="primary"
            disabled={loading}
            loading={loading}
          >
            Cambiar contrasea
          </Button>
        </Form.Item>
      </Form>
    </>
  );
};

export default ChangePassword;
